@import 'imports/adg-theme/theme';

/* Simple spinner icon for wait/loading time */
.aui-icon-wait {
    background-image: url(data:image/gif;base64,);
}

.aui-dropdown2 .active .aui-icon-wait,
.aui-button-primary .aui-icon-wait {
    background-image: url(data:image/gif;base64,);
}

.aui-button-primary[aria-disabled="true"] .aui-icon-wait {
    background-image: url(data:image/gif;base64,);
}

.aui-icon-success,
.aui-icon-warning,
.aui-icon-info,
.aui-icon-error,
.aui-icon-hint,
.aui-icon-generic {
    height: @aui-icon-size-small;
    width: @aui-icon-size-small;
    line-height: 0;
    position: relative;

    &::before {
        color: #707070;
        content: "\f16f";
        font-size: @aui-icon-size-small;
        font-family: @aui-icon-font-family;
        font-weight: normal;
        -webkit-font-smoothing: antialiased; /* Improves the rendering of icons */
        font-style: normal;
        left: 0;
        line-height: 1;
        margin-top: -8px; /* (font-size/2) */
        position: absolute;
        speak: none; /* This prevents screen readers from pronouncing the pseudo element text content used to trigger the icon font */
        text-indent: 0;
        top: 50%;
    }
}

/* Message icons */
.aui-icon-generic:before {
    color: #707070;
    content: "\f23d";
}

.aui-icon-error:before {
    color: #d04437;
    content: "\f15a";
}

.aui-icon-hint:before,
.aui-icon-info:before {
    color: #707070;
    content: "\f23d";
}

.aui-icon-success:before {
    color: #14892c;
    content: "\f194";
}

.aui-icon-warning:before {
    color: #f6c342;
    content: "\f1b3";
}
